<script setup>
import { Handle, Position } from "@vue-flow/core";
</script>
<script>
export default {
  name: "TimeTriggerNode",
  props: {
    id: String,
    isSelected: Boolean,
    data: Object,
  },
  created() {},
  data() {
    return {
      label: this.data.label,
      nodeData: this.data,
      isFold: true,
      isFold_inputs: true,
      isFold_TimeTrigger: true,
      TimeTriggerList: this.data.TimeTrigger
        ? this.data.TimeTrigger.map((item, i) => {
            item.top = i * 25 + 125;
            item.top_str = "top:" + item.top + "px";
            return item;
          })
        : [],
      loopType: [
        { name: "每隔n秒", data: "s", text: "每n秒" },
        { name: "每隔n天", data: "d", text: "每n天" },
        { name: "每周星期几", data: "w", text: "每周星期几" },
        { name: "每月第几日", data: "m", text: "每月第几日" },
      ],
    };
  },
  methods: {
    resetHandleTop() {
      var vHeight = 70;
      if (this.isFold_inputs) {
        vHeight = vHeight + 55;
      }
      if (this.isFold_TimeTrigger && this.isFold) {
        if (this.isFold || this.isFold_inputs) {
          this.TimeTriggerList = this.TimeTriggerList.map((item, i) => {
            item.top = i * 25 + vHeight;
            item.top_str = "top:" + item.top + "px";
            return item;
          });
        }
      } else {
        this.TimeTriggerList = this.TimeTriggerList.map((item) => {
          item.top = 0;
          item.top_str = "";
          return item;
        });
      }
    },
    saveNode() {
      this.$emit("saveNode", {
        id: this.id,
        data: { ...this.nodeData, label: this.label, TimeTrigger: this.TimeTriggerList },
      });
    },
    saveLabel() {
      this.saveNode();
    },
    checkFold() {
      this.isFold = !this.isFold;
      this.resetHandleTop();
    },
    checkFoldInputs() {
      this.isFold_inputs = !this.isFold_inputs;
      this.resetHandleTop();
    },
    checkFoldTimeTrigger() {
      this.isFold_TimeTrigger = !this.isFold_TimeTrigger;
      this.resetHandleTop();
    },
    onAdd() {
      var id = this.TimeTriggerList.length + 1;
      var top = this.TimeTriggerList.length * 25 + 125;
      this.TimeTriggerList.push({
        id: "Handle_" + id,
        inputs: "2",
        loopType: "",
        value: "",
        top: top,
        top_str: "top:" + top + "px",
      });
      this.saveNode();
    },
    onDel(TimeTrigger) {
      this.TimeTriggerList = this.TimeTriggerList.filter(
        (item) => item.id !== TimeTrigger.id
      );
      this.saveNode();
    },
  },
};
</script>

<template>
  <div v-for="item in TimeTriggerList" :key="item.id">
    <Handle
      :id="item.id"
      class="Handle"
      type="source"
      :position="Position.Right"
      :style="item.top_str"
    />
  </div>
  <div
    class="NodeBox TimeTriggerNode iconfont"
    :class="[isSelected ? 'Selected' : '', '']"
  >
    <div class="Title">
      <span
        class="Icon fold"
        :class="[isFold ? 'icon-jiantou-d' : 'icon-jiantou-r']"
        @click="checkFold"
      ></span>
      <span class="Icon ticon icon-TimeTrigger"></span>
      <span>时间触发器</span>
      <input
        class="label"
        v-model="label"
        value="{{label}}"
        placeholder="备注"
        @blur="saveLabel"
      />
    </div>
    <div class="Content" v-if="isFold">
      <div class="Description">可以定义一个特定时间点来触发下个节点</div>

      <div class="TimeTrigger Content">
        <div class="Title">
          <span
            class="Icon fold"
            :class="[isFold_TimeTrigger ? 'icon-jiantou-d' : 'icon-jiantou-r']"
            @click="checkFoldTimeTrigger"
          ></span>
          <span>触发周期</span>
        </div>
        <div v-if="isFold_TimeTrigger">
          <table>
            <thead>
              <th colspan="2">周期</th>
              <th>第一次开始时间</th>
              <th>执行次数</th>
              <th>删除</th>
            </thead>
            <tbody>
              <tr v-for="item in TimeTriggerList" :key="item.id">
                <td>
                  <select v-model="item.loopType">
                    <option
                      v-for="_item in loopType"
                      :key="_item.name"
                      :value="_item.data"
                    >
                      {{ _item.text }}
                    </option>
                  </select>
                </td>
                <td><input type="number" placeholder="数值" v-model="item.Interval" /></td>
                <td>
                  <input type="datetime" placeholder="留空代表立即执行" v-model="item.StartTime" />
                </td>
                <td><input type="number" placeholder="留空代表不限次数" v-model="item.Repeat" /></td>
                <td>
                  <span class="Icon ticon icon-del del" @click="onDel(item)"></span>
                </td>
              </tr>
            </tbody>
            <footer>
              <tr>
                <td colspan="4" class="add" @click="onAdd">
                  <span class="Icon ticon icon-add"></span>
                  <span>增加条件</span>
                </td>
              </tr>
            </footer>
          </table>
        </div>
      </div>
    </div>
  </div>
</template>
