<script setup>
import { Handle, Position } from "@vue-flow/core";
import Multiselect from "vue-multiselect";
import SliderInput from "../SliderInput.vue";
</script>
<script>
export default {
  name: "LargeModelNode",
  props: {
    id: String,
    isSelected: Boolean,
    data: Object,
    largeModel: Object,
    plugins: Object,
  },
  components: { Multiselect, SliderInput },
  data() {
    return {
      label: this.data.label,
      nodeData: this.data,
      largeModelList: this.largeModel,
      pluginsList: this.plugins,
      isFold: true,
      isFold_inputs: true,
      isFold_outputs: true,
      isFold_prompt: true,
      isFold_largemodel: true,
      isFold_plugins: true,
      isFold_plugins2: true,
      isFold_Coefficient: true,
    };
  },
  methods: {
    saveNode() {
      this.$emit("saveNode", {
        id: this.id,
        data: { ...this.nodeData, label: this.label },
      });
    },
    saveLabel() {
      this.saveNode();
    },
    checkFold() {
      this.isFold = !this.isFold;
    },
    checkFoldInputs() {
      this.isFold_inputs = !this.isFold_inputs;
    },
    checkFoldOutputs() {
      this.isFold_outputs = !this.isFold_outputs;
    },
    checkFoldPrompt() {
      this.isFold_prompt = !this.isFold_prompt;
    },
    checkFoldLargeModel() {
      this.isFold_largemodel = !this.isFold_largemodel;
    },
    checkFoldPlugins() {
      this.isFold_plugins = !this.isFold_plugins;
    },
    checkFoldPlugins2() {
      this.isFold_plugins2 = !this.isFold_plugins2;
    },
    checkCoefficient() {
      this.isFold_Coefficient = !this.isFold_Coefficient;
    },
  },
};
</script>
<template>
  <Handle class="Handle" type="target" :position="Position.Left" :style="{}" />
  <Handle class="Handle" type="source" :position="Position.Right" :style="{}" />
  <div
    class="NodeBox LargeModelNode iconfont"
    :class="[isSelected ? 'Selected' : '', '']"
  >
    <div class="Title">
      <span
        class="Icon fold"
        :class="[isFold ? 'icon-jiantou-d' : 'icon-jiantou-r']"
        @click="checkFold"
      ></span>
      <span class="Icon ticon icon-LargeModel"></span>
      <span>大语言模型</span>
      <input
        class="label"
        v-model="label"
        value="{{label}}"
        placeholder="备注"
        @blur="saveLabel"
      />
    </div>
    <div class="Content" v-if="isFold">
      <div class="Description">调用大语言模型，处理变量，配合提示词，生成输出</div>
      <div class="LargeModel Content">
        <div class="Title">
          <span
            class="Icon fold"
            :class="[isFold_largemodel ? 'icon-jiantou-d' : 'icon-jiantou-r']"
            @click="checkFoldLargeModel"
          ></span>
          <span>模型</span>
        </div>
        <div v-if="isFold_largemodel">
          <select v-model="nodeData.model.LargeModelID">
            <option
              v-for="(item, i) in largeModelList"
              :key="i"
              :value="item.LargeModelID"
            >
              {{ item.ModelOrganizationName }}({{ item.Name }})
            </option>
          </select>
        </div>
      </div>

      <div class="Inputs Content">
        <div class="Title">
          <span
            class="Icon fold"
            :class="[isFold_plugins ? 'icon-jiantou-d' : 'icon-jiantou-r']"
            @click="checkFoldPlugins"
          ></span>
          <span>提示词函数(插件)</span>
        </div>
        <div v-if="isFold_plugins">
          <multiselect
            v-model="nodeData.model.SemanticFunctionID"
            placeholder="请选择函数"
            :multiple="true"
            :close-on-select="false"
            :clear-on-select="false"
            :preserve-search="true"
            label="Name"
            track-by="Name"
            taggable="true"
            :options="pluginsList.filter((value) => value.PluginsType == 1)"
          ></multiselect>
        </div>
      </div>
      <div class="Plugins Content">
        <div class="Title">
          <span
            class="Icon fold"
            :class="[isFold_plugins2 ? 'icon-jiantou-d' : 'icon-jiantou-r']"
            @click="checkFoldPlugins2"
          ></span>
          <span>原生函数(插件)</span>
        </div>
        <div v-if="isFold_plugins2">
          <multiselect
            v-model="nodeData.model.NativeFunctionID"
            placeholder="请选择函数"
            :multiple="true"
            :close-on-select="false"
            :clear-on-select="false"
            :preserve-search="true"
            label="Name"
            track-by="Name"
            taggable="true"
            :options="pluginsList.filter((value) => value.PluginsType == 2)"
          ></multiselect>
        </div>
      </div>

      <div class="Inputs Content">
        <div class="Title">
          <span
            class="Icon fold"
            :class="[isFold_inputs ? 'icon-jiantou-d' : 'icon-jiantou-r']"
            @click="checkFoldInputs"
          ></span>
          <span>输入</span>
        </div>
        <div v-if="isFold_inputs">
          <table>
            <thead>
              <th>变量名</th>
              <th>描述</th>
            </thead>
            <tbody>
              <tr v-for="(item, i) in nodeData.inputs" :key="i">
                <td><input v-model="item.varname" /></td>
                <td><input v-model="item.txt" /></td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
      <div class="Prompt Content">
        <div class="Title">
          <span
            class="Icon fold"
            :class="[isFold_prompt ? 'icon-jiantou-d' : 'icon-jiantou-r']"
            @click="checkFoldPrompt"
          ></span>
          <span>提示词（将输入变量与提示词整理，并让AI重新生成返回结果）</span>
        </div>
        <div v-if="isFold_prompt">
          <textarea
            v-model="nodeData.prompt"
            :placeholder="[
              '请输入提示词：' +
                nodeData.inputs.map((item) => '{{' + item.varname + '}}').join(','),
            ]"
          ></textarea>
        </div>
      </div>
      <div class="Coefficient Content">
        <div class="Title">
          <span
            class="Icon fold"
            :class="[isFold_Coefficient ? 'icon-jiantou-d' : 'icon-jiantou-r']"
            @click="checkCoefficient"
          ></span>
          <span>系数设置</span>
        </div>
        <div v-if="isFold_Coefficient">
          <div>
            <span>随机性(值越大越有创造力)</span
            ><SliderInput
              v-model="nodeData.temperature"
              min="0"
              max="100"
            />
          </div>
          <div>
            <span>多样性(值越大越有多样性)</span
            ><SliderInput
              v-model="nodeData.topp"
              min="0"
              max="100"
            />
          </div>
        </div>
      </div>
      <div class="Outputs Content">
        <div class="Title">
          <span
            class="Icon fold"
            :class="[isFold_outputs ? 'icon-jiantou-d' : 'icon-jiantou-r']"
            @click="checkFoldOutputs"
          ></span>
          <span>输出</span>
        </div>
        <div v-if="isFold_outputs">
          <table>
            <thead>
              <th>变量名</th>
              <th>描述</th>
            </thead>
            <tbody>
              <tr v-for="(item, i) in nodeData.output" :key="i">
                <td><input v-model="item.varname" /></td>
                <td><input v-model="item.txt" /></td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
  </div>
</template>
